<template>
  <div>
    <h3>repository配置</h3>
    <el-row :gutter="20">
      <el-col :span="5">
        包名：
        <el-input placeholder="mapper包名" class="repository-package" value="mapper"
                  v-model="repositorySetting.repositoryPackage"/>
      </el-col>
      <el-col :span="6">
        类名后缀：
        <el-input placeholder="类名后缀" class="class-sufix" v-model="repositorySetting.repositorySufix"/>
      </el-col>
      <el-col :span="13">
        <el-col :span="12">
          <el-checkbox label="@Repository注解" v-model="repositorySetting.useRepositoryAnonntation"/>
        </el-col>
      </el-col>
    </el-row>


  </div>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapState} from "vuex";

  export default {
    name: "Project",
    computed: {
      ...mapGetters(["repositorySetting", "projectSetting"]),
      ...mapState(["repositorySetting"]),
    }
  }
</script>

<style scoped>
  @import "../../../static/css/content.css";

  .class-sufix {
    width: 45%;
  }

  .repository-package {
    width: 52%;
  }
</style>



